<template>
    <div id="login">        
        <div class="backround_image_box">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-sm-4"></div>
                    <div class="col-sm-4 login_box">                        
                        <form action="#" @submit.prevent="login" class="login-form">
                            <div class="form-group">
                                <input type="email" class="form-control" placeholder="Email" v-model="email">                            
                            </div>
                            <div class="form-group">
                                <input type="password" class="form-control" placeholder="Password" v-model="password">                            
                            </div>
                            <div class="form-group">
                                <button type="submit" class="btn btn-default login-submit form-control">SIGN IN</button>
                            </div>
                            <div class="form-group forgot_password_link">
                                <a href="#">Forgot Password</a>
                            </div>
                        </form>
                    </div>
                    <div class="col-sm-4"></div>
                 </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data () {
        return {
            email: '',
            password: ''
        }   
    },

    methods: {
        login() {
            this.$store.dispatch('retriveToken', {
                email: this.email,
                password: this.password
            })
            .then(response => {
                this.$router.push('/')
            })
        }
        
    }
}
</script>

<style>
.page_header {
  width: 100%;
  height: 100px;  
}

.logo_img{
  padding: 20px;
  height:200px;
  width: 250px;
}

.backround_image_box{
  background: url('../assets/images/background_image.jpg') no-repeat center center; 
 -webkit-background-size: cover;
 -moz-background-size: cover;
 -o-background-size: cover;
 background-size: cover;
 width:  100%;
 display: table;
 display: table;
 padding: 180px 0;
}

.login_box{
  background-color: #f3f3f3;
}

.login-form {
  padding: 20px;
}

.login-form .form-control {
  height: 46px;
  border: 0px solid #fff;
  border-radius: 0px;
}

.login-submit{
  background-color: #67971a;
  color: #ffffff;
}

.forgot_password_link{
  text-align: right;
  margin-bottom: 38px;
}
</style>


