<template>
  <div v-if="this.$store.state.authenticated">
    <app-header />
      <div class="container mb-5">
        <router-view />
      </div>
    <app-footer/>
  </div>
  <div v-else>    
    <router-view />
    <app-footer/>
  </div>
</template>

<script>

import axios from 'axios';

// import views from ./components
import Header from './components/Header.vue';
import Footer from './components/Footer.vue';
import Home from './components/Home.vue';
import Reasons from './components/reason/Reasons.vue';
import NewReason from './components/reason/NewReason.vue';
import EditReason from './components/reason/EditReason.vue';
import Projects from './components/project/Projects.vue';
import NewProject from './components/project/NewProject.vue';
import EditProject from './components/project/EditProject.vue';
import Companies from './components/company/Companies.vue';
import NewCompany from './components/company/NewCompany.vue';
import EditCompany from './components/company/EditCompany.vue';
import Users from './components/user/Users.vue';
import NewUser from './components/user/NewUser.vue';
import EditUser from './components/user/EditUser.vue';
import Employees from './components/employee/Employees.vue';
import NewEmployee from './components/employee/NewEmployee.vue';
import EditEmployee from './components/employee/EditEmployee.vue';
import Login from './components/Login.vue';
import { access } from 'fs';


export default {
  // register local components
  components: {
    'app-header': Header,
    'app-footer': Footer,
    'app-home': Home,
    'app-reason': Reasons,
    'app-newReason': NewReason,
    'app-editReason': EditReason,
    'app-project': Projects,
    'app-newProject': NewProject,
    'app-editProject': EditProject,
    'app-company': Companies,
    'app-newCompany': NewCompany,
    'app-editCompany': EditCompany,
    'app-user': Users,
    'app-newUser': NewUser,
    'app-editUser': EditUser,
    'app-employee': Employees,
    'app-newEmployee': NewEmployee,
    'app-editEmployee': EditEmployee,
    'app-login': Login
  },
  data () {
    return {

    }
  },

  methods: {
    retriveToken() {
      if (!this.$store.state.authenticated) {
        this.$router.push('/login')
      } else {
        this.$router.push('/')
      }
    }
  },

  mounted() {
    this.retriveToken()
  }
}
</script>

